/* stylelint-disable no-descending-specificity */

@mixin thumb-base {
  margin-top: rem(-11.5px);
  background-color: var(--major-accent__enabled__fill__default);
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  transition: 0.3s ease-in-out;

  &:hover {
    background-color: var(--major-accent__enabled__fill__hovered);
  }

  &:active {
    background-color: var(--major-accent__enabled__fill__pressed);
  }
}

@mixin track-base {
  // the --progress css variable is set through javascript
  background: linear-gradient(
    to right,
    var(--color) 0%,
    var(--color) var(--progress),
    var(--small__enabled__fill__default) var(--progress),
    var(--small__enabled__fill__default)
  );
  height: 0.13rem;
}

@mixin track-base-disabled {
  // the --progress css variable is set through javascript
  background: linear-gradient(
    to right,
    var(--major-accent__disabled__fill__default) 0%,
    var(--major-accent__disabled__fill__default) var(--progress),
    var(--small__disabled__fill__default) var(--progress),
    var(--small__disabled__fill__default)
  );
}

.a-slider {
  height: rem(48px);
  width: auto;

  div {
    display: flex;
    position: relative;
    width: 100%;
    flex: 1 1 auto;
    margin: 1.5rem 0.75rem;
  }

  display: flex;
  align-items: center;

  input {
    height: 0.13rem;
    width: 100%;
    -webkit-appearance: none;
    background-color: var(--small__enabled__fill__default);
    outline: 0;

    --color: #{var(--major-accent__enabled__fill__default)};

    &::-webkit-slider-thumb {

      @include thumb-base;

      -webkit-appearance: none;
    }

    &::-webkit-slider-runnable-track {

      @include track-base;
    }

    &::-moz-range-thumb {

      @include thumb-base;

      border: 0;
    }

    &::-moz-range-track {

      @include track-base;
    }


    &:disabled {
      &::-webkit-slider-thumb {
        background-color: var(--major-accent__disabled__fill__default);
      }

      &::-moz-range-thumb {
        background-color: var(--major-accent__disabled__fill__default);
      }

      &::-webkit-slider-runnable-track {

        @include track-base-disabled;
      }

      &::-moz-range-track {

        @include track-base-disabled;
      }
    }
  }

  .a-tooltip {
    text-align: center;
    position: absolute;
    bottom: 1.5rem;
    white-space: nowrap;
    visibility: hidden;
  }

  label {
    font-size: rem(16px);
    flex: 0 1 auto;
  }

  &.a-slider--labels-on-top {
    flex-wrap: wrap;

    // the labels should not shrink below 50%, but can grow as much as they want
    > label {
      order: 1;
      flex: 1 0 50%;
    }

    // the div containing the input should not shrink below 100%, and should be
    // the third element, visually
    // remove the margins around the input so that it aligns with the label's edges
    > div,
    > input {
      order: 3;
      flex: 1 0 100%;
      margin-left: 0;
      margin-right: 0;
    }

    // the label after the input container should be the second element (visually),
    // and the text should right-align
    > div + label,
    > input + label {
      order: 2;
      text-align: right;
    }

    > input {
      margin: rem(24px) 0;
    }
  }
}
