.m-language-selector {
  display: flex;

  label {
    font-size: rem(16px);
    white-space: nowrap;
  }

  .a-icon {
    margin-right: rem(4px);
  }

  .a-dropdown {
    margin-left: rem(12px);
    height: auto;

    select {
      padding-left: rem(6px);
      padding-right: rem(26px);
      height: 1.75em;
      margin-top: -0.5em;
    }

    &::after {
      top: 0;
      right: rem(2px);
    }
  }
}
